import { Box, Image, Text } from '@chakra-ui/react'
import React from 'react'
import BTC from '../assests/btc.jpg'
import { motion } from "framer-motion"

function Home() {
    return (
        <>
            <Box bgColor={"black"} w={"full"} h={"85vh"}>
                <motion.div
                    style={{
                        height: "80vh",
                    }}
                    animate={{
                        translateY: "20px",
                    }}
                    transition={{
                        duration: 1,
                        repeat: Infinity,
                        repeatType: "reverse"
                    }}
                >

                    <Image w={"full"} h={"full"} objectFit={"contain"} src={BTC} />
                </motion.div>
                <Text
                    fontSize={"6xl"}
                    textAlign={"center"}
                    fontWeight={"thin"}
                    color={"whiteAlpha.600"}
                    mt={"3"} //margin top
                >Xcrypto</Text>
            </Box >
        </>
    )
}

export default Home
