import React, { useEffect, useState } from 'react';
import axios from "axios";
import { server } from '../index';
import { Container, HStack, Heading, Image, VStack, Text } from '@chakra-ui/react';
import Loader from './Loader';
import ErrorComponents from './ErrorComponents';

function Exchanges() {

    const [exchanges, setExchanges] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(false);

    useEffect(() => {
        const fetchExchange = async () => {
            try {
                const { data } = await axios.get(`${server}/exchanges`);
                console.log(data)
                setExchanges(data)
                setLoading(false)
            } catch (error) {

                setError(true);
                setLoading(false);

            }
        };
        fetchExchange();
    }, [])


    if (error)
        return <ErrorComponents message={"Error While Fetching Exchange"} />;

    return (
        <>
            <Container maxW={"Container.xl"}>

                {loading ? (<Loader />) : (<>

                    <HStack wrap={"wrap"} justifyContent={"space-evenly"}>
                        {exchanges.map((props) => (
                            <ExchangeCard
                                key={props.id}
                                name={props.name}
                                img={props.image}
                                rank={props.trust_score_rank}
                                url={props.url}
                            />
                        ))}
                    </HStack>
                </>
                )};
            </Container>

        </>
    )
}


const ExchangeCard = ({ name, img, rank, url }) => (
    <a href={url} target={'blank'}>
        <VStack w={"52"} shadow={"lg"} p={"8"} borderRadius={"lg"} transition={"all 0.3s"} m={"4"} css={{
            "&:hover": {
                transform: "scale(1.1)",
            },
        }}>
            <Image src={img} w={"10"} h={"10"} objectFit={"contain"} alt={"Exchange"} />
            <Heading size={"md"} noOfLines={1}>{rank}</Heading>
            <Text noOfLines={1}>{name}</Text>
        </VStack>
    </a>
)





export default Exchanges
