import { Badge, Box, Button, Container, HStack, Image, Progress, Radio, RadioGroup, Stat, StatArrow, StatLabel, StatNumber, Text, VStack } from '@chakra-ui/react'
import React, { useEffect, useState } from 'react'
import Loader from './Loader';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { server } from '../index';
import ErrorComponents from './ErrorComponents';
import Chart from './Chart';

function CoinDetails() {

    const [coin, setCoin] = useState({});
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(false);
    const [currency, setCurrency] = useState("inr");
    const [days, setDays] = useState("24"); //for chart........................
    const [chartArry, setchartArry] = useState([]); //for chart........................


    const currencySymbol =
        currency === "inr" ? "₹" : currency === "eur" ? "€" : "$";

    const btns = ["24h", "7d", "14d", "30d", "60d", "200d", "365d", "max"];

    const switchChartStates = (key) => {

        switch (key) {
            case "24h":
                setDays("24h");
                setLoading(true)
                break;

            case "7d":
                setDays("7d");
                setLoading(true)
                break;

            case "14d":
                setDays("14d");
                setLoading(true)
                break;

            case "30d":
                setDays("30d");
                setLoading(true)
                break;

            case "60d":
                setDays("60d");
                setLoading(true)
                break;

            case "200d":
                setDays("200d");
                setLoading(true)
                break;

            case "365d":
                setDays("365d");
                setLoading(true)
                break;

            case "max":
                setDays("max");
                setLoading(true)
                break;
            default:
                setDays("24h");
                setLoading(true)
                break;
        }
    }



    const params = useParams();

    useEffect(() => {
        const fetchCoin = async () => {
            try {
                const { data } = await axios.get(`${server}/coins/${params.id}`); //for coins

                const { data: chartData } = await axios.get(`${server}/coins/${params.id}/market_chart?vs_currency=${currency}&days=${days}`); //for chart or coin details

                // console.log(chartData)
                setchartArry(chartData.prices) //for chart
                setCoin(data)
                setLoading(false)
            } catch (error) {

                setError(true);
                setLoading(false);

            }
        };
        fetchCoin();
    }, [params.id, currency, days])

    if (error)
        return <ErrorComponents message={"Error While Fetching Coin"} />;

    return (
        <Container maxW={'container.lg'} >
            {
                loading ? (
                    <Loader />
                ) : (
                    <>
                        {/* chart......................................... */}
                        <Box width={"full"} borderWidth={1}>
                            <Chart arr={chartArry} currency={currencySymbol} days={days} />
                        </Box>

                        {/* button */}
                        <HStack p="4" overflowX={"auto"}>
                            {
                                btns.map((i) => (
                                    <Button key={i} onClick={() => switchChartStates(i)}>{i}</Button>
                                ))
                            }

                        </HStack>
                        <RadioGroup value={currency} onChange={setCurrency} p={"8"}>
                            <HStack spacing={"4"}>
                                <Radio value={"inr"}>INR</Radio>
                                <Radio value={"usd"}>USD</Radio>
                                <Radio value={"eur"}>EUR</Radio>
                            </HStack>
                        </RadioGroup>
                        {/* market_data.last_updated ---> it is copid from the data to get the exact date */}
                        <VStack spacing={"4"} p={"16"} alignItems={"flex-start"}>
                            <Text fontSize={"small"} alignItems="center" opacity={0.7}>
                                Last Updated On {" "}
                                {Date(coin.market_data.last_updated).split("G")[0]}
                            </Text>
                            <Image src={coin.image.large} width={"16"} h={"16"} objectFit={"contain"}></Image>

                            <Stat>
                                <StatLabel>{coin.name}</StatLabel>
                                <StatNumber>{currencySymbol}{coin.market_data.current_price[currency]}</StatNumber>
                                <StatArrow
                                    type={
                                        coin.market_data.price_change_percentage_24h > 0
                                            ? "increase"
                                            : "decrease"
                                    }
                                >
                                    {coin.market_data.price_change_percentage_24h}%
                                </StatArrow>
                            </Stat>

                            <Badge
                                fontSize={"2xl"}
                                bgColor={"blackAlpha.600"}
                                color={"white"}
                            >{`#${coin.market_cap_rank}`}</Badge>
                            {/*  //-------------->Custombar  */}
                            <CustomBar high={`${currencySymbol}${coin.market_data.high_24h[currency]}`}
                                low={`${currencySymbol}${coin.market_data.low_24h[currency]}`} />

                            {/*--------->>>Item */}
                            <Box w={"full"} p={"4"}>
                                <Item title={"max Supply"} value={coin.market_data.max_supply} />
                                <Item title={"Circulating Supply"} value={coin.market_data.circulating_supply} />
                                <Item title={"Market Cap"} value={`${currencySymbol}${coin.market_data.market_cap[currency]} `} />
                                <Item title={"All Time Low"} value={`${currencySymbol}${coin.market_data.atl[currency]}`} />
                                <Item title={"All Time High"} value={`${currencySymbol}${coin.market_data.ath[currency]}`} />


                            </Box>
                        </VStack>
                    </>
                )
            }
        </Container >
    );
};

const CustomBar = ({ high, low }) => {    //-------------->Custombar
    return (<>
        <VStack w={'full'}>
            <Progress value={50} colorScheme={"teal"} w={"full"} />
            <HStack justifyContent={"space-between"} w={"full"}>
                <Badge children={low} colorScheme={'red'} />
                <Text fontSize={"sm"}>24hr Range</Text>
                <Badge children={high} colorScheme={'green'} />
            </HStack>
        </VStack>
    </>)
};


const Item = ({ title, value }) => (  //--------->>>Item
    <HStack justifyContent={"space-between"} w={"full"} my={"4"}>
        <Text fontFamily={"Oswald"} letterSpacing={"widest"}>
            {title}
        </Text>
        <Text>{value}</Text>
    </HStack>
)

export default CoinDetails
